<template>
  <div id="container-{{uploadType}}">
    <mdl-button v-mdl-ripple-effect id="button-{{uploadType}}">{{uploadName}}</mdl-button>
    <input type="hidden" id="uptoken_url" value="{{uptoken}}">
  </div>
</template>

<script>
import Qiniu from 'Qiniu'
import FileService from './FileUploadService.js'

export default {
  props: {
    uploadType: String,
    container: String,
    uploadName: String
  },
  data () {
    return {
      uploader: null,
      uptoken: '',
      domain: '',
      prefix: ''
    }
  },
  ready: function () {
    var self = this
    console.log(self)
    var filename = ''
    var promise = FileService.getToken(filename, self.uploadType)
    promise.then(function (response) {
      // success callback
      var result = response
      self.uptoken = result.token
      self.domain = result.domain
      self.key = result.key
      self.prefix = result.prefix
      self.uploader = Qiniu.uploader({
        prefix: self.prefix,
        runtimes: 'html5,flash,html4',       // ,
        upload_type: self.uploadType,
        browse_button: 'button-' + self.uploadType,          // ****
        // uptoken_url: '/token',            // AjaxupTokenUrl****
        uptoken: self.uptoken,
        // unique_names: true, //  falsekeySDKkey
        // save_key: true,   //  falseuptoken `sava_key`SDKkey
        domain: self.domain,   // bucket ****
        get_new_uptoken: true,            // token
        container: 'container-' + self.uploadType,            // DOM IDbrowser_button
        max_file_size: '100mb',            // 
        // flash_swf_url: 'js/plupload/Moxie.swf',  // flash,
        max_retries: 3,                    // 
        dragdrop: false,                   // 
        // drop_element: 'container',      // ID
        chunk_size: '4mb',                 // 
        auto_start: true,                 // 
        init: {
          'FilesAdded': function (up, files) {
            // console.log('FilesAdded')
            // for (var i = 0; i < files.length; i++) {
            //   var file = files[i]
            //   console.log(file.name)
            // }
            // console.log(up.id)
            // console.log(up.getOption().domain)
          },
          'BeforeUpload': function (up, file) {
            // ,
            // console.log('BeforeUpload')
            // console.log(file.name)
          },
          'UploadProgress': function (up, file) {
            // ,
            console.log('' + file.name)
            console.log(up.getOption().domain)
            console.log(up.getOption().uptoken)
          },
          'FileUploaded': function (up, file, info) {
            console.log('FileUploaded')
            var domain = up.getOption('domain')
            console.log(info)
            var res = JSON.parse(info)
            var sourceLink = 'http://' + domain + '/' + res.key     // Url
            var data = {
              'type': up.getOption('upload_type'),
              'link': sourceLink,
              'updatePath': file.id + '_' + file.name
            }
            console.log(self)
            console.log(data)
            self.$dispatch('uploaded', data)
            console.log('---------------')
            // ,
            //  info json
            // {
            //    "hash": "Fh8xVqod2MQ1mocfI4S4KpRL6D98",
            //    "key": "gogopher.jpg"
            //  }
            // http://developer.qiniu.com/docs/v6/api/overview/up/response/simple-response.html

            // var domain = up.getOption('domain');
            // var res = parseJSON(info);
            // var sourceLink = domain + res.key; Url
          },
          'Error': function (up, err, errTip) {
            // ,
            console.log('error')
            console.log(err)
            console.log(errTip)
          },
          'UploadComplete': function () {
            // ,
            console.log('UploadComplete')
          },
          'Key': function (up, file) {
            // console.log('-----------------')
            // console.log(this)
            // console.log('---------------')
            // console.log(self)
            // console.log(self.prefix)
            // key
            //  unique_names: false , save_key: false 
            // keykey
            // console.log('key')
            var key = up.getOption().prefix + file.id + '_' + file.name
            // console.log(key)
            // var key = "zhiyin/content/audio/732204597354958800.png";
            // do something with key here
            return key
          }
        }
      })
    }, function (response) {
      // error callback
      console.log('error')
    })
  }
}
</script>






